import React, { useEffect } from "react";
import { getLocations } from "../../api/content";
import "./map.css";
import AMapLoader from "@amap/amap-jsapi-loader";

function Map({ setCurrent }) {
  useEffect(() => {
    const getData = async () => {
      try {
        const res = await getLocations();
        loadAmap(res.data);
      } catch (error) {
        console.log(error);
      }
    };
    getData();
    return () => {};
  }, []);
  const loadAmap = (locations) => {
    AMapLoader.load({
      key: "e8906766d91ba9d1ab24cb4d0b20cc0e",
      version: "2.0",
      plugins: ["AMap.Scale", "AMap.ToolBar"],
    })
      .then((AMap) => {
        const map = new AMap.Map("container", {
          layers: [new AMap.TileLayer.Satellite()],
          zoom: 7,
        });
        // const center = map.getCenter();
        // const { lat, lng } = center;
        // map.add(
        //   new AMap.Marker({
        //     position: [lng, lat],
        //     icon: "//a.amap.com/jsapi_demos/static/demo-center/marker/marker-bottom-left.png",
        //     imageSize: new AMap.Size(12, 12),
        //   })
        // );
        map.getCity((n) => {
          n && setCurrent(n);
        });
        map.addControl(new AMap.Scale());
        map.addControl(new AMap.ToolBar());
        locations.forEach((l) => {
          const position = l.position.split(";").reverse();
          const marker = new AMap.Marker({
            position,
            icon: new AMap.Icon({
              image: require(`../../assets/icons/${l.activity.name}.svg`)
                .default,
              imageSize: new AMap.Size(24, 24),
            }),
          });
          // marker.setLabel({
          //   direction: "right",
          //   offset: new AMap.Pixel(10, 0), //设置文本标注偏移量
          //   content: l.name, //设置文本标注内容
          // });
          map.add(marker);
        });
      })
      .catch((e) => {
        console.log(e);
      });
  };
  return <div className="map-container" id="container"></div>;
}
export default Map;
